.navbar{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 16px;
    position: sticky;
    top: 0;
    z-index: 200;
    background-color: #dcdcdc;
}

.navbar.change{
    border-bottom: 1px solid #dcdcdc;
    background-color: #1b1c38;
}

.navbar_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    background-color: #dcdcdc;
    padding: 10px 16px;
    border-radius: 0 0 10px 10px;
    transition: 0.1s ease-out;
}

.navbar.change .navbar_container{
    background-color: #1b1c38;
}

.navbar_container .logo{
    width: 100%;
    max-width: 150px;
}

.navbar_container .logo img{
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 8px;
}

.navbar_container ul{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 40px;
    list-style: none;
}

.navbar_container ul li a{
    text-decoration: none;
    color: #3f4b4b;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    transition: 0.3s ease-in-out;
    position: relative;
}

.navbar.change .navbar_container ul li a,
.navbar.change .navbar_container ul li a{
    color: #d4d1c9;
}

.navbar_container ul li a:hover,
.navbar_container ul li.active a{
    color: #14282A;
}

.navbar.change .navbar_container ul li a:hover,
.navbar.change .navbar_container ul li.active a{
    color: #bf9312;
}

.navbar_container ul li.active a::after,
.navbar_container ul li a:hover::after{
    content: "";
    position: absolute;
    height: 2px;
    width: 30px;
    border-radius: 6px;
    background-color: #14282A;
    bottom: -3px;
    left: 0;
}

.navbar.change .navbar_container ul li.active a::after,
.navbar.change .navbar_container ul li a:hover::after{
    background-color: #bf9312;
}

.navbar_container a.button{
    text-decoration: none;
    white-space: nowrap;
    color: #1b1c38;
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    padding: 8px 16px;
    border: 2px solid #1b1c38;
    border-radius: 8px;
    transition: 0.3s ease-in-out;
    background-color: transparent;
}

.navbar_container a.button:hover,
.navbar_container a.button.active{
    color: #bfbfbf;
    background-color: #1b1c38;
}

.navbar.change .navbar_container a.button,
.navbar.change .navbar_container a.button{
    color: #bf9312;
    border: 2px solid #bf9312;
}

.navbar.change .navbar_container a.button:hover,
.navbar.change .navbar_container a.button.active{
    color: #bfbfbf;
    background-color: #bf9312;
}

.desktop{
    display: block;
}

.mobile{
    display: none;
}

@media screen and (max-width:950px) {
    .desktop{
        display: none;
    }
    
    .mobile{
        display: block;
    }

    .navbar_container ul{
        width: 80%;
        max-width: 400px;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 0;
        right: -1000px;
        height: 100vh;
        background-color: #f8f8f8;
        padding: 32px;
        z-index: 201;
        transition: 0.3s ease-out;
        border-left: 1px solid #f8f8f8;
    }

    .navbar.change .navbar_container ul{
        background-color: #1b1c38;
    }

    .navbar_container ul.open{
        right: 0;
    }

    .navbar .navbar_container .bars{
        transform: rotate(0deg);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 202;
        transition: 0.3s ease-out;
    }

    .navbar .navbar_container .bars svg{
        height: 24px;
        width: 24px;
        color: #14282A;
        cursor: pointer;
    }

    .navbar .navbar_container .bars.rotate{
        transform: rotate(180deg);
    }

    .navbar.change .navbar_container .bars svg{
        color: #bfbfbf;
    }
}

@media screen and (max-width:650px) {
    .navbar{
        padding: 0;
    }
}