.stripe{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px 32px;
}

.stripe .container{
    width: 100%;
    max-width: 1200px;
    min-height: 50vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 80px;
    z-index: 2;
}

.stripe .container form{
    display: flex;
    width: 100%;
    max-width: 750px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 32px 16px;
    border-radius: 8px;
    border: 2px solid #1b1c38;
    gap: 32px;
}

.stripe .container form h1{
    white-space: nowrap;
    color: #1b1c38;
    font-size: 20px;
    font-weight: 600;
    text-transform: capitalize;
    line-height: 1.5em;
    letter-spacing: 0.15px;
}

.stripe .container form .row{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 16px;
}

.stripe .container form .row .inputs{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    width: 100%;
}

.stripe .container form .row .inputs label{
    color: #637c7e;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.15px;
}

.stripe .container form .row .inputs input,
.stripe .container form .row .inputs select{
    width: 100%;
    border: none;
    outline: none;
    padding: 8px 10px;
    border-radius: 6px;

    color: #637c7e;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    cursor: pointer;
}

.stripe .container form .row .inputs .error{
    color: rgb(255, 38, 0);
}

.stripe .container form .row.loader{
    width: 100%;
    height: 2px;
    position: relative;
    overflow: hidden;
}

.stripe .container form .row.loader span{
    display: block;
    width: 25%;
    height: 100%;
    background-color: transparent;
    animation: load 3s ease-in-out infinite;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
}

.stripe .container form .row.loader.load{
    background-color: #637c7e;
}

.stripe .container form .row.loader.load span{
    background-color: rgb(255, 38, 0);
}

@keyframes load {
    0%{
      transform: translateX(0%);
    }
    50%{
      transform: translateX(250%);
    }
    100%{
      transform: translateX(400%);
    }
  }

@media screen and (max-width:650px) {
    .stripe{
        padding: 80px 16px;
    }
}

@media screen and (max-width:550px){
    .stripe .container form .row{
        flex-direction: column-reverse;
    }

    .stripe .container form .row .inputs{
        align-items: center;
    }
}