.footer{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 32px 20px;
    background-color: #1b1c38;
    z-index: 10;
    position: relative;
}

.footer_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
    justify-content: space-between;
}

.footer_container .upper{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    gap: 50px;
}

.footer_container .upper .left{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}

.footer_container .upper .left .logo{
    width: 100%;
    max-width: 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.footer_container .upper .left .logo img{
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-left: -16px;
}

.footer_container .upper .left .logo span{
    width: 60%;
    height: 2px;
    background-color: #bf9312;
}

.footer_container .upper .left p{
    color: #637c7e;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    width: 100%;
    max-width: 400px;
}

.footer_container .upper .left p span{
    text-transform: uppercase;
}

.footer_container .upper .right{
    display: flex;
    margin-top: 65px;
}

.footer_container .upper .left .list,
.footer_container .upper .right .list{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}

.footer_container .upper .left h1,
.footer_container .upper .right h1{
    white-space: nowrap;
    color: #bfbfbf;
    font-size: 20px;
    font-weight: 600;
    text-transform: capitalize;
    line-height: 1.5em;
    letter-spacing: 0.15px;
}

.footer_container .upper .left ul,
.footer_container .upper .right ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.footer_container .upper .left ul li,
.footer_container .upper .right ul li{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    color: #637c7e;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
}

.footer_container .upper .right ul li img{
    width: 35px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    border-radius: 8px;
}

.footer_container .upper .left ul li svg{
    width: 20px;
    height: 20px;
    color: #bf9312;  
}
.footer_container .upper .right ul li svg{
    width: 18px;
    height: 18px;
    color: #bf9312;
}

.footer_container .upper .left ul li a,
.footer_container .upper .right ul li a{
    color: #637c7e;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    text-decoration: none;
    transition: 0.3s ease-in-out;
}
.footer_container .upper .left ul li a{
    font-size: 18px;
}

.footer_container .upper .left ul li a:hover,
.footer_container .upper .right ul li a:hover,
.footer_container .upper .right ul li.active a{
    color: #bf9312;
}

.footer_container .upper .left .line,
.footer_container .upper .right .line{
    display: block;
    width: 60px;
    height: 2px;
    background-color: #bf9312;
    margin-top: -15px;
}

.footer_container .lower{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    gap: 20px;
}

.footer_container .lower span{
    width: 100%;
    height: 1px;
    background-color: #637c7e;
}

.footer_container .lower section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 15px;
    width: 100%;
}

.footer_container .lower section p{
    color: #bfbfbf;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    text-align: center;
}

@media screen and (max-width:750px) {
    .footer_container .upper{
        flex-direction: column;
    }

    .footer_container .upper .right{
        margin-top: 0;
    }

    .footer_container .lower section{
        justify-content: center;
    }

    .footer_container .upper .left p{
        text-align: center;
    }

    .footer_container .upper .left,
    .footer_container .upper .left .list,
    .footer_container .upper .left .list ul,
    .footer_container .upper .right,
    .footer_container .upper .right .list,
    .footer_container .upper .right .list ul{
        flex-direction: column;
        align-items: center;
        width: 100%;
        justify-content: center;
    }

    .footer_container .upper .right ul li a{
        text-align: center;
    }

    .footer_container .upper .left .logo{
        align-items: center;
    }
}

@media screen and (max-width:650px) {
    .footer{
        padding: 20px 16px 20px;
    }
}