.landlords_hero{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 32px;
}

.landlords_hero_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 80px 32px;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    gap: 30px;
    position: relative;
}

.landlords_hero_container div{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.507);
    z-index: 1;
    border-radius: 20px;
}

.landlords_hero_container h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #e8f3f7;
    font-size: 84px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.05em;
    z-index: 2;
    text-transform: capitalize;
}

.landlords_hero_container span{
    z-index: 2;
    width: 70px;
    height: 2px;
    background-color: #bf9312;
}

.landlords_hero_container p{
    font-size: 28px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0em;
    width: 100%;
    max-width: 72%;
    color: #fff;
    font-family: 'Hanken Grotesk', sans-serif;
    margin-bottom: 80px;
    z-index: 2;
}

@media screen and (max-width:750px){
    .landlords_hero_container h1{
        font-size: 32px;
    }

    .landlords_hero_container p{
        font-size: 16px;
        max-width: 75%;
    }

    .landlords_hero_container{
        padding: 80px 12px 80px 16px;
    }
}

@media screen and (max-width:650px) {
    .landlords_hero{
        padding: 20px 16px;
    }
}

@media screen and (max-width:650px){
    .landlords_hero_container p{
        max-width: none;
    }
}