.options{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 32px 80px;
}

.options_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
}

.options_container .item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 30px;
}

.options_container .item.reverse{
    flex-direction: row-reverse;
}

.options_container .item .image{
    width: 100%;
    max-width: 450px;
    height: 350px;
    background-color: black;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.options_container .item .content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 14px;
    width: 100%;
}

.options_container .item .content h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #000;
    font-size: 32px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.025em;
    text-transform: uppercase;
}

.options_container .item .content table, .options_container .item .content th, .options_container .item .content td {
    border: 1px solid #14282A;
    border-collapse: collapse;
}

.options_container .item .content table{
    border: 3px solid #14282A;
}

.options_container .item .content th{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #14282A;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.2em;
    letter-spacing: -0.01em;
    padding: 5px;
}

.options_container .item .content td{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #14282A;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: -0.01em;
    padding: 5px;
}

.options_container .item .content p,
.options_container .item .content a{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #14282A;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: -0.01em;
}

.options_container .item .content a{
    color: #1b1c38;
    text-decoration: underline;
}

.options_container .item .content a:hover{
    color: #bf9312;
}

.options_container .item .content span{
    width: 75px;
    height: 2px;
    background-color: #bf9312;
}

@media screen and (max-width:650px) {

    .options{
        padding: 100px 16px 80px;
    }

    .options_container .item,
    .options_container .item.reverse{
        flex-direction: column;
        align-items: flex-start;
    }

    .options_container .item .image{
        max-width: none;
    }

    .options_container .item .content h1{
        font-size: 24px;
    }

    .options_container .item .content p{
        font-size: 16px;
    }
}