.service{
    width: 100%;
    padding: 0 32px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.service_container{
    width: 100%;
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 20px;
}

.service_container .item:nth-child(2){
    grid-row: auto / 2 span;
    justify-content: flex-start;
    align-items: center;
}

.service_container .item{
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 20px;
    padding: 130px 20px 25px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 12px;
    position: relative;
}

.service_container .item .back{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    background-color: #000000c5;
    border-radius: 20px;
}

.service_container .item section{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 18px;
    z-index: 2;
}

.service_container .item section h1{
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1.3em;
    letter-spacing: 0.55px;
    transition: 0.3s ease-in-out;
}

.service_container .item section p{
    color: #bfbfbf;
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 1.3em;
    letter-spacing: 0.15px;
    transition: 0.3s ease-in-out;
}

.service_container .item a{
    text-decoration: none;
    white-space: nowrap;
    color: #bfbfbf;
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    padding: 8px 16px;
    border: 2px solid #bfbfbf;
    border-radius: 8px;
    transition: 0.3s ease-in-out;
    background-color: transparent;
    cursor: pointer;
    z-index: 2;
}

.service_container .item a:hover{
    color: #bf9312;
    background-color: #1b1c38;
    border: 2px solid #bf9312;
}

@media screen and (max-width:1050px) {
    .service_container{
        grid-template-columns: repeat(2,1fr);
    }
    
    .service_container .item:nth-child(2){
        grid-column: auto / 2 span;
        grid-row: auto / 1 span;
    }

    .service_container .item:nth-child(3){
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 0;
        grid-row-end: 1;
    }
}

@media screen and (max-width:650px) {

    .service{
        padding: 0 16px 100px;
    }

    .service_container{
        grid-template-columns: repeat(1,1fr);
    }

    .service_container .item:nth-child(2){
        grid-column: auto / 1 span;
        grid-row: auto / 1 span;
    }

    .service_container .item:nth-child(3){
        grid-column-start: auto;
        grid-column-end: auto;
        grid-row-start: 2;
        grid-row-end: 3;
    }
}