.payment{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px 32px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;    
}

.payment .blur{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    /* background-color: #000000cd; */
    z-index: 1;
}

.payment_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 180px;
    z-index: 2;
}

.payment_container .item{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.payment_container .item div{
    border-radius: 20px;
    padding: 20px;
    border: 1px solid #999999;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 900px;
    gap: 20px;
}

.payment_container .item.reverse{
    align-items: flex-end;
}

.payment_container .item h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #232222;
    font-size: 32px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.025em;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
}

.payment_container .item h1 span{
    width: 75px;
    height: 2px;
    background-color: #bf9312;
}

.payment_container .item p{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #524f4f;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2em;
    letter-spacing: -0.01em;
    width: 100%;
    max-width: 900px;
}

.payment_container .item .button{
    text-decoration: none;
    white-space: nowrap;
    color: #1b1c38;
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    padding: 8px 16px;
    border: 2px solid #1b1c38;
    border-radius: 8px;
    transition: 0.3s ease-in-out;
    background-color: white;
}

.payment_container .item .button:hover{
    color: white;
    background-color: #1b1c38;
}

