*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Hanken Grotesk', sans-serif;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
  color: inherit;
}

.whatsapp{
  position: fixed;
  bottom: 40px;
  right: 40px;
  z-index: 1000;
}

.whatsapp a{
  width: 100px;
  height: 100px;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #25d366;
  border-radius: 8px;
  color: white;
}

.whatsapp a svg{
  width: 100%;
  height: 100%;
}

p{
  text-align: justify;
}

.pay-btn{
  display: flex;
  justify-content: center;
  align-items: center;
    width: 100px;
    text-decoration: none;
    white-space: nowrap;
    color: #bfbfbf;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    line-height: 1.5em;
    letter-spacing: 0.15px;
    padding: 8px 16px;
    background-color: #1b1c38;
    border: 1px solid #161a61;
    border: none;
    outline: none;
    border-radius: 8px;
    transition: 0.3s ease-in-out;
    cursor: pointer;
    z-index: 2;
}

.pay-btn:hover{
  background-color: #161a61;
}

.pay-btn:disabled{
  cursor: not-allowed;
  background-color: #bfbfbf;
  color: #161a61;
}

.pay{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 80px 32px;
}

.pay .container{
  width: 100%;
  max-width: 1200px;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 80px;
  z-index: 2;
}

.pay.success .container h1{
  color: #00de51;
}

.pay.failed .container h1{
  color: rgb(201, 48, 48);
}

@media screen and (max-width:650px) {
  .pay{
      padding: 80px 16px;
  }

  .whatsapp a{
    width: 50px;
    height: 50px;
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
  margin: 0;
}