.provide{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 32px 80px;
}

.provide_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 60px;
}

.provide_container h1{
    font-size: 42px;
    font-weight: 400;
    line-height: 1em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
}

.provide_container h1 span{
    width: 70px;
    height: 2px;
    background-color: #bf9312;
}

.provide_container .item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap: 40px;
    align-items: center;
    flex-direction: row;
}

.provide_container .item.reverse{
    flex-direction: row-reverse;
}

.provide_container .item .content{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.provide_container .item.reverse .content{
    align-items: flex-end;
}

.provide_container .item .content h1{
    color: #000;
    font-size: 26px;
    font-weight: 600;
    line-height: 1.3em;
    letter-spacing: 0.25px;
    text-transform: uppercase;
}

.provide_container .item .content p{
    color: #000;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.3em;
    letter-spacing: 0.15px;
    text-align: left;
}

.provide_container .item .content p a{
    color: #1b1c38;
    text-decoration: underline;
}

.provide_container .item .content p a:hover{
    color: #bf9312;
}

.provide_container .item.reverse .content p{
    text-align: right;
}

.provide_container .item .image{
    width: 100%;
    max-width: 340px;
    height: 250px;
    background-color: black;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 20px;
}

@media screen and (max-width:750px){
    .provide_container .item{
        align-items: flex-start;
        flex-direction: column;
    }
    
    .provide_container .item.reverse{
        flex-direction: column;
    }

    .provide_container .item.reverse .content p{
        text-align: left;
    }

    .provide_container .item.reverse .content{
        align-items: flex-start;
    }

    .provide_container .item .image{
        max-width: none;
    }
}

@media screen and (max-width:650px) {
    .provide{
        padding: 0 16px 80px;
    }

    .provide_container h1{
        font-size: 32px;
    }

    .provide_container .item .content p{
        font-size: 16px;
    }

    .provide_container .item .content h1{
        font-size: 20px;
    }
}