.home_about{
    width: 100%;
    margin:100px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 32px;
}

.home_about_container{
    width: 100%;
    max-width: 1200px;
    gap: 32px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.home_about_container .left{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 30px;
}

.home_about_container .left h1{
    color: #0b0b0b;
    font-size: 54px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.025em;
    text-transform: uppercase;
}

.home_about_container .left p{
    color: #14282a;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.5em;
    letter-spacing: 0.03em;
}

.home_about_container .right{
    width: 100%;
    height: 100%;
}

.home_about_container .right img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

.home_about_container .left .buttom{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
}

.home_about_container .left .buttom .option{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;
    background-color: #dcdcdc;
}

.home_about_container .left .buttom .option.active{
    background-color: #dcdcdc;
}

.home_about_container .left .buttom .option .line{
    height: 100%;
    width: 4px;
    background-color: #7daae6;
    transition: 0.3s ease-in-out;
}

.home_about_container .left .buttom .option.active .line{
    background-color: #bf9312;
}

.home_about_container .left .buttom .option .contentWrapper{
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: flex-start;
    cursor: pointer;
}

.home_about_container .left .buttom .option .circle{
    display: block;
    width: 20px;
    min-width: 20px;
    margin-top: 10px;
    height: 20px;
    border-radius: 50%;
    background-color: #dcdcdc;
    border: 1px solid #0b0b0b;
}

.home_about_container .left .buttom .option.active .circle{
    background-color: #bf9312;
    border: 1px solid #bf9312;
}

.home_about_container .left .buttom .option .contentWrapper .content{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.home_about_container .left .buttom .option .contentWrapper .content h1{
    font-size: 30px;
    line-height: 1.3em;
    font-weight: 600;
    letter-spacing: -0.025em;
    text-transform: uppercase;
}

.home_about_container .left .buttom .option.active .contentWrapper .content h1{
    color: #bf9312;
}

.home_about_container .left .buttom .option .contentWrapper .content p{
    transition: 0.3s ease-in-out;
    overflow: hidden;
    font-size: 18px;
    font-weight: 400;
}

@media screen and (max-width:750px) {
    .home_about_container{
        grid-template-columns: repeat(1,1fr);
    }

    .home_about_container .left{
        width: 100%;
    }
}

@media screen and (max-width:650px) {
    .home_about{
        padding: 0 16px;
    }

    .home_about_container .left .buttom .option .contentWrapper .content h1{
        font-size: 22px;
    }

    .home_about_container .left h1{
        font-size: 46px;
    }
    
    .home_about_container .left p{
        font-size: 16px;
    }
}