.resource{
    width: 100%;
    padding: 0 32px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.resource_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}

.resource_container .upper{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 20px;
}

.resource_container .upper .left{
    width: 100%;
    height: 100%;
    background-color: #f7ece8;
    border-radius: 20px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: 80px;
}

.resource_container .upper .left .top{
    display: flex;
    flex-direction: column;
    gap: 20px;
    transform: translateY(0px);
    opacity: 1;
    transition: 0.5s ease-out;
}

.resource_container .upper .left.move .top{
    transform: translateY(50px);
    opacity: 0;
}

.resource_container .upper .left .top p{
    font-size: 27px;
    font-weight: 400;
    font-style: italic;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
}

.resource_container .upper .left .top span{
    display: block;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.resource_container .upper .left .top span svg{
    width: 40px;
    height: 40px;
    color: #bf9312;
}

.resource_container .upper .left img{
    width: 77.8px;
    height: 77.8px;
    object-fit: cover;
    border-radius: 20px;
}

.resource_container .upper .left .bottom{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    transform: translateY(0px);
    opacity: 1;
    transition: 0.5s ease-out;
}

.resource_container .upper .left.move .bottom{
    transform: translateY(-50px);
    opacity: 0;
}

.resource_container .upper .left .bottom div{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.resource_container .upper .left .bottom div h2{
    font-size: 27px;
    font-weight: 600;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
}

.resource_container .upper .left .bottom div p{
    font-size: 18px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
}

.resource_container .upper .right{
    width: 100%;
    height: 100%;
    border-radius: 20px;
    padding: 50px;
    background-color: #eae8e8;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    gap: 80px;
    position: relative;
}

.resource_container .upper .right .top{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    position: absolute;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    background-color: #0000006b;
    border-radius: 20px;
}

.resource_container .upper .right .top h1{
    font-size: 27px;
    font-weight: 600;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #fff;
    font-family: 'Hanken Grotesk', sans-serif;
}

.resource_container .upper .right .top p{
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #fff;
    font-family: 'Hanken Grotesk', sans-serif;
}

.resource_container .upper .right a{
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #bf9312;
    text-decoration: underline;
    font-family: 'Hanken Grotesk', sans-serif;
    display: flex;
    align-items: center;
    gap: 8px;
    z-index: 2;
}

.resource_container .upper .right a svg{
    width: 18px;
    height: 18px;
}

.resource_container .upper .right a:hover{
    color: #bf9312;
}

.resource_container .lower{
    width: 100%;
    padding: 50px;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content:space-around;
    align-items: center;
    position: relative;
}

.resource_container .lower .background{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    background-color: #00000099;
    border-radius: 20px;
}

.resource_container .lower h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #ffffff;
    font-size: 64px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.05em;
    width: 120%;
    z-index: 2;
}

.resource_container .lower h1 a{
    color: #bf9312;
    font-size: 24px;
    letter-spacing: 0;
    text-decoration: underline;
}

.resource_container .lower div{
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.resource_container .lower div a{
    padding: 20px 40px;
    color: #FFF;
    background-color: #14282A;
    border: none;
    outline: none;
    border-radius: 32px;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    line-height: 1em;
    letter-spacing: 0em;
    cursor: pointer;
}

.resource_container .lower div a:hover{
    color: #e8f3f7;
    background-color: #0b0b0b;
}

@media screen and (max-width:990px) {
    .resource_container .lower h1{
        font-size: 34px;
    }

    .resource_container .lower h1 a{
        font-size: 16px;
    }

    .resource_container .upper .left .top p,
    .resource_container .upper .right .top h1{
        font-size: 22px;
    }

    .resource_container .upper .right .top p,
    .resource_container .upper .right a{
        font-size: 18px;
    }

    .resource_container .upper .left .bottom div p{
        font-size: 16px;
    }
}

@media screen and (max-width:750px) {
    .resource_container .upper .left,
    .resource_container .upper .right,
    .resource_container .lower{
        padding: 50px 20px;
    }

    .resource_container .upper{
        grid-template-columns: repeat(1,1fr);
    }
}

@media screen and (max-width:650px) {
    .resource{
        padding: 0 16px 100px;
    }

    .resource_container .lower{
        flex-direction: column;
        gap: 20px;
    }

    .resource_container .lower h1,
    .resource_container .lower div{
        width: 100%;
        text-align: center;
    }
}