.hero{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 32px;
}

.hero_container{
    width: 100%;
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(2,1fr);
    gap: 20px;
}

.hero .hero_container .left{
    width: 100%;
    padding: 50px;
    border-radius: 20px;
    background-color: #CCE0E9;
    position: relative;
    grid-row: auto / span 2;
    grid-column: auto / span 2;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 50px;
}

.hero .hero_container .left p{
    font-size: 28px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    margin-bottom: 40px;
    z-index: 3;
}

.hero .hero_container .left h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #0b0b0b;
    font-size: 84px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.05em;
    z-index: 3;
}

.hero .hero_container .right{
    width: 100%;
    height: 100%;
    background-color: #eae8e8;
    border-radius: 20px;
    padding: 40px 25px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    justify-content: space-around;
}

.hero .hero_container .right h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #0b0b0b;
    font-size: 28px;
    font-weight: 700;
    letter-spacing: -0.05em;
    z-index: 3;
}

.hero .hero_container .right p{
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    margin-bottom: 40px;
    z-index: 3;
}

.hero .hero_container a{
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0em;
    color: #1b1c38;
    font-family: 'Hanken Grotesk', sans-serif;
    z-index: 3;
    text-decoration: underline;
}

.hero .hero_container a:hover{
    color: #bf9312;
}

.hero .hero_container .right.down{
    position: relative;
} 

/* .hero .hero_container .right.down h1,
.hero .hero_container .right.down p{
    color: white;
} */

.hero .hero_container .right.down .back{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
    background-color: #ffffffc3;
    border-radius: 20px;
}

@media screen and (max-width:1000px) {

    .hero .hero_container .left{
        padding: 20px;

    }

    .hero .hero_container .right{
        padding: 40px 15px;
    }

    .hero .hero_container .right h1{
        font-size: 24px;
        font-weight: 600;
    }

    .hero .hero_container .right p{
        font-size: 18px;
        font-weight: 400;
    }

    .hero .hero_container .left p{
        font-size: 20px;
        line-height: 1.5em;
        letter-spacing: 0em;
    }

    .hero .hero_container .left h1{
        font-size: 60px;
        line-height: 1em;
        letter-spacing: -0.05em;
    }
}

@media screen and (max-width:750px) {

    .hero_container{
        grid-template-columns: repeat(1,1fr);
        grid-template-rows: repeat(2,1fr);
    }

    .hero .hero_container .left{
        grid-row: auto / span 2;
        grid-column: auto / span 1;
    }

    .hero .hero_container .right{
        grid-row: auto / span 1;
        grid-column: auto / span 1;
    }
}

@media screen and (max-width:650px) {

    .hero{
        padding: 0 16px;
    }

    .hero .hero_container .left h1{
        max-width: 100%;
        padding-right: 10px;
    }
}

@media screen and (max-width:467px) {
    .hero .hero_container .left p{
        max-width: 100%;
        padding-right: 10px;
        margin-bottom: 0;
    }
}