.highlight{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 32px 80px;
}

.highlight_container{
    width: 100%;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 80px;
}

.highlight_container .item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
}

.highlight_container .item.reverse{
    flex-direction: row-reverse;
}

.highlight_container .top{
    margin-top: -40px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    max-width: 800px;
    border-radius: 20px;
    padding: 20px;
    border: 1px solid #000000;
}

.highlight_container .top h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #000;
    font-size: 32px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.025em;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
}

.highlight_container .top h1 span{
    width: 75px;
    height: 2px;
    background-color: #bf9312;
}

.highlight_details{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #14282A;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: -0.01em;
}

.highlight_container .item .image{
    width: 100%;
    max-width: 450px;
    height: 350px;
    background-color: black;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.highlight_container .item .content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 14px;
    width: 100%;
}

.highlight_container .item .content h1{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #000;
    font-size: 32px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: -0.025em;
    text-transform: uppercase;
}

.highlight_container .item .content p,
.highlight_container .item .content a{
    font-family: 'Hanken Grotesk', sans-serif;
    color: #14282A;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2em;
    letter-spacing: -0.01em;
}

.highlight_container .item .content a{
    color: #1b1c38;
    text-decoration: underline;
}

.highlight_container .item .content a:hover{
    color: #bf9312;
}

.highlight_container .item .content span{
    width: 75px;
    height: 2px;
    background-color: #bf9312;
}

@media screen and (max-width:650px) {

    .highlight{
        padding: 100px 16px 80px;
    }

    .highlight_container .item,
    .highlight_container .item.reverse{
        flex-direction: column;
        align-items: flex-start;
    }

    .highlight_container .item .image{
        max-width: none;
    }

    .highlight_container .item .content h1{
        font-size: 24px;
    }

    .highlight_container .item .content p{
        font-size: 16px;
    }
}