.landlords{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 32px;
}

.landlords_container{
    width: 100%;
    max-width: 1200px;
    display:grid;
    grid-template-columns: repeat(2,1fr);
    gap: 40px;
}

.landlords_container .left{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}

.landlords_container .left p{
    font-size: 17px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
}

.landlords_container .left img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 8px;
}

.landlords_container .right{
    width: 100%;
    max-width: 520px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 20px;
}

.landlords_container .right h1{
    font-size: 42px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    text-transform: uppercase;
}

.landlords_container .right .line{
    width: 70px;
    height: 2px;
    background-color: #bf9312;
}

.landlords_container .right .wrapper{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 60px;
    margin: 40px 0 0 42.5px;
    border-left: 2px solid #bf9312;
}

.landlords_container .right .wrapper .list{
    display: flex;
    align-items: center;
    gap: 42px;
    min-height: 120px;
    margin: 0 0 0 -42.5px;
}

.landlords_container .right .wrapper .list .icon{
    width: 85px;
    height: 85px;
    min-width: 85px;
    max-height: 85px;
    background-color: #c7c9c9;
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    rotate: 45deg;
    transition: 0.3s ease-in-out;
}

.landlords_container .right .wrapper .list:nth-child(1) .icon{
    align-self: flex-start;
}

.landlords_container .right .wrapper .list:nth-child(3) .icon{
    align-self: flex-end;
}

.landlords_container .right .wrapper .list .icon:hover{
    background-color: #1b1c38;
}

.landlords_container .right .wrapper .list .icon svg{
    width: 24px;
    height: 24px;
    color: #bf9312;
    rotate: -45deg;
}

.landlords_container .right .wrapper .list .content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
}

.landlords_container .right .wrapper .list .content h2{
    font-size: 26px;
    font-weight: 600;
    line-height: 1em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    text-transform: uppercase;
}

.landlords_container .right .wrapper .list .content p{
    font-size: 17px;
    font-weight: 400;
    line-height: 1.5em;
    letter-spacing: 0em;
    color: #14282A;
    font-family: 'Hanken Grotesk', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}

.landlords_container .right .wrapper .list .content p a{
    color: #1b1c38;
    text-decoration: underline;
}

.landlords_container .right .wrapper .list .content p a:hover{
    color: #bf9312;
    text-decoration: underline;
}

@media screen and (max-width:900px){
    .landlords_container{
        grid-template-columns: repeat(1,1fr);
    }

    .landlords_container .right{
        max-width: none;
    }
}

@media screen and (max-width:650px) {
    .landlords{
        padding: 100px 16px;
    }

    .landlords_container .right h1{
        font-size: 32px;
    }

    .landlords_container .right .wrapper .list .content h2{
        font-size: 20px;
    }

    .landlords_container .right .wrapper .list .content p{
        font-size: 15px;
    }
}